th {
    display: flex;
    margin: 5%;
    border: 1px solid rgb(167, 166, 166);
    padding: 2%;
}

th:hover {
    cursor: pointer;
}

tbody tr {
    display: flex;
    width: 100%;
}

tbody tr td {
    display: flex;
    width: 100%;
    height: 50%;
    flex: 1;
    justify-content: center;
}
